import React, { useState } from "react";
import "./styles.css";

export default function App() {
  const [count, setStr2] = useState(0);
  const header = count === 2 ? null : Example;

  return (
    <div className="App">
      {header && header instanceof Function && header(count, setStr2)}
      <h1>Hello CodeSandbox</h1>
      <h2>Start editing to see some magic happen!</h2>
    </div>
  );
}

function Example(el, setStr2) {
  const [count, setCount] = useState(0);

  return (
    <div>
      <p>You clicked {count} times</p>
      <p>{el}</p>
      <button
        onClick={() => {
          setCount(count + 1);
          setStr2(count);
        }}
      >
        Click me
      </button>
    </div>
  );
}
